.contact-form{
    display: grid;
    grid-template-columns: 1fr;
    padding:10%;
}

.contact-form-label {
    padding-bottom: 2%;
    padding-top: 2%;
    font-family: "Spartan League", sans-serif;
    font-weight: 600;
}

.contact-form-input {
    color: #000;
    border: 1px solid gray;
    background-color: #F0EEED;
    border-radius: 5px;
    padding: 1rem;
    font-family: "Open Sans", sans-serif;
    font-weight: 500;
    letter-spacing: 0.5px;
    line-height: 1.5;
}

.submit-button {
    margin-top: 5%;
}

textarea {
    resize: none;
}

.contact-heading {
    padding-left: 10%;
    padding-right: 10%;
}