.home-page {
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url('../../public/images/background-pic.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    height: 100vh;
    text-align: center;
    color: #FFFFFF;
}

.title-div {
    position: relative;
    top: 40vh;
    font-family: "Spartan League", sans-serif;
}


.title h1 {
    margin: 0;
    font-size: 4.5rem;
}

.home-list { 
    list-style-type: none;
    display: grid;
    grid-template-columns:  repeat(3, 1fr);
    padding: 2% 20%;
    font-size: 24px;
}

.home-list a {
    text-decoration: none;
    color: #FFFFFF;
    border: 1px solid #FFFFFF;
    border-radius: 5px;
    padding: 2%;
    letter-spacing: 1px;
}

.home-list a:hover {
    background-color: #FFFFFF;
    color: #000000; 
}